// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import Element from 'element-ui'
import App from './App'
import wangEditor from 'wangEditor'//

import './common/fontsize/iconfont.css'

//css
import 'element-ui/lib/theme-default/index.css'
import './common/css/reset.css'

//
import point from './components/pointManagement/point'//

import MyCase from './components/my-case/my-case'   //
import editor from './components/editor/editor'//
import statiStic from './components/CaseStatistics/statistics'//
import RejectCase from './components/reject-case/reject-case'   //
import AuditCase from './components/audit-case/audit-case'  //
import NotAuditCase from './components/not-audit-case/not-audit-case'//
import authority from './components/authorityManagement/AuthorityManagement'//
import CollectCase from './components/collect-case/collect-case'   //
import pstatistics  from './components/poinman/ppstatistics'//
import startRevie  from './components/not-audit-case/to-audit'//
import AggregateStatistics from './components/CaseStatistics/aggregate-statistics'//
import groupAggregateStatistics from './components/CaseStatistics/group-aggregate-statistics'  //
import drafts from './components/drafts/Drafts'//
import AccountInfo from './components/account-info/account-info'   //
import caseDetail from './components/caseDetail/case-detail'   //


Vue.use(VueRouter)//
Vue.use(VueResource)//HTTP
Vue.use(Element)//Element
const routes = [

{
		path:'/point',
		component:point//
},{
		path:'/editor',
		component:editor//
},{
	  path:'/statistics',
		component:statiStic//
},{
		path:'/authority',
		component:authority//
},{
		path:'/MyCase',//
		component:MyCase,
},{
		path:'/MyCase/editor',
		component:editor//
},{
    path: '/accountInfo',
    name: 'AccountInfo',
    component: AccountInfo
  },
  {
    path: '/rejectCase',
    name: 'RejectCase',
    component: RejectCase//
  },
  {
    path: '/auditCase',
    name: 'AuditCase',
    component: AuditCase
  },//
   {
    path: '/notAuditCase',
    name: 'NotAuditCase',
    component: NotAuditCase,
  }//
  ,{
  	name:'startReview',
  	path:'/notAuditCase/startReview',
    	component:startRevie//
  },{
    path: '/collectCase',
    name: 'CollectCase',
    component: CollectCase
  },
  {
    path: '/pstaTistics',
    name: 'CollectCase',
    component: pstatistics
},
{
	path:'/statistics/aggregate-statistics',
	component:AggregateStatistics //
},{
	path:'/statistics/group-aggregate-statistics',
	component:groupAggregateStatistics//
},{
	path:'/statistics/MyCase/drafts',
	component:drafts//
},{
	path:'/casecase/case-detail',
	component:caseDetail//
}
];//

const router = new VueRouter({
  routes
});//



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  ...App
}) //vue
Vue.config.debug = true;


